import kotlinx.browser.window
import kotlinx.coroutines.*
import kotlin.js.Promise
import kotlin.random.Random


class Letters(
    private val letterFrequency: Map<String, Float>
) {

    fun random(): String {
        val randomNumber = Random.nextDouble() * letterFrequency.values.sum()
        var probabilityIterator = 0.0
        letterFrequency.forEach {
            probabilityIterator += it.value
            if (probabilityIterator >= randomNumber) {
                return it.key
            }
        }
        throw IllegalStateException("Couldn't get random letter")
    }
}

object LetterFrequencyLoader {
    private const val DELIMITER = ";"
    private const val LETTER_COL = "Letter"
    private const val FREQUENCY_COL = "Frequency"

    suspend fun fetch(language: Language): Letters {
        return Promise.resolve(window.fetch("/letters_${language.code}.csv"))
            .then { it.text() }
            .then { parse(it) }
            .await()
    }

    private fun parse(csv: String): Letters {
        val lines = csv.lines()
        val header = lines.first().split(DELIMITER)
        val letterIndex = header.indexOf(LETTER_COL)
        val frequencyIndex = header.indexOf(FREQUENCY_COL)

        val letterToFrequency = lines
            .drop(1)
            .filter { it.isNotBlank() }
            .map { it.split(DELIMITER) }
            .associate { it[letterIndex] to it[frequencyIndex].toFloat() }

        return Letters(letterToFrequency)
    }
}

enum class Language(val code: String) {
    POLISH("pl")
}