import BoardDimensions.ALL_Xs
import BoardDimensions.ALL_Ys
import BoardDimensions.COLUMNS
import BoardDimensions.ROWS

class WordMatcher(private val words: Words) {
    fun match(xys: List<OccupiedXY>): Matched {
        val xyToLetter = xys.associate { it.xy to it.letter }
        return Matched(rows = rowsMatched(xyToLetter), columns = columnsMatched(xyToLetter))
    }

    private fun rowsMatched(xyToLetter: Map<XY, String>): List<Int> {
        return ALL_Ys.filter { y ->
            val strings = ALL_Xs.joinToString("") { x -> xyToLetter[XY(x, y)] ?: " " }.split(" ")
            val matchedWords = strings.flatMap { wordsMatchedInString(it) }
            if (matchedWords.isNotEmpty()) console.log("Matched: $matchedWords")
            matchedWords.isNotEmpty()
        }
    }

    private fun columnsMatched(xyToLetter: Map<XY, String>): List<Int> {
        return ALL_Xs.filter { x ->
            val strings = ALL_Ys.joinToString("") { y -> xyToLetter[XY(x, y)] ?: " " }.split(" ")
            val matchedWords = strings.flatMap { wordsMatchedInString(it) }
            if (matchedWords.isNotEmpty()) console.log("Matched: $matchedWords")
            matchedWords.isNotEmpty()
        }
    }

    private fun wordsMatchedInString(string: String): List<String> {
        return allPossibleSubstrings(string).filter(words::matches)
    }

    private fun allPossibleSubstrings(string: String): List<String> {
        return when {
            string.length < MIN_LENGTH -> emptyList()
            string.length == MIN_LENGTH -> listOf(string)
            else -> listOf(string) + allPossibleSubstrings(string.drop(1)) + allPossibleSubstrings(string.dropLast(1))
        }
    }

    data class Matched(val rows: List<Int>, val columns: List<Int>) {
        fun isEmpty() = rows.isEmpty() && columns.isEmpty()
    }

    companion object {
        const val MIN_LENGTH = 3
    }
}