data class Tetromino(
    val shape: Shape,
    val localXYs: List<OccupiedXY>
) {

    companion object {
        fun random(letters: Letters) = listOf(I(letters), T(letters), O(letters), J(letters), L(letters), S(letters), Z(letters)).random()
        fun I(letters: Letters) = tetromino(Shape.I, listOf(XY(1, 0), XY(1, 1), XY(1, 2), XY(1, 3)), letters)
        fun O(letters: Letters) = tetromino(Shape.O, listOf(XY(1, 1), XY(1, 2), XY(2, 1), XY(2, 2)), letters)
        fun T(letters: Letters) = tetromino(Shape.T, listOf(XY(0, 1), XY(1, 1), XY(2, 1), XY(1, 0)), letters)
        fun J(letters: Letters) = tetromino(Shape.J, listOf(XY(0, 1), XY(1, 1), XY(2, 1), XY(0, 0)), letters)
        fun L(letters: Letters) = tetromino(Shape.L, listOf(XY(0, 1), XY(1, 1), XY(2, 1), XY(2, 0)), letters)
        fun S(letters: Letters) = tetromino(Shape.S, listOf(XY(0, 1), XY(1, 1), XY(1, 0), XY(2, 0)), letters)
        fun Z(letters: Letters) = tetromino(Shape.Z, listOf(XY(0, 0), XY(1, 0), XY(1, 1), XY(2, 1)), letters)
        private val PIVOT = XY(1, 1)
        private fun tetromino(shape: Shape, localXYs: List<XY>, letters: Letters) =
            Tetromino(shape, localXYs.map {
                OccupiedXY(
                    xy = it,
                    isPivot = it == PIVOT,
                    shape = shape,
                    letter = letters.random()
                )
            })
    }

    enum class Shape { I, O, T, J, L, S, Z }
}