data class OccupiedXY(
    val xy: XY,
    val isPivot: Boolean,
    val shape: Tetromino.Shape,
    val letter: String
) {
    operator fun plus(other: XY) = copy(xy = xy + other)
    fun move(direction: Direction, distance: Int = 1) = copy(xy = xy.move(direction, distance))
    fun valid(): OccupiedXY? = xy.valid()?.let { copy(xy = xy) }
}
