import BoardDimensions.ALL_Xs
import BoardDimensions.ALL_Ys
import kotlinx.browser.document
import BoardDimensions.COLUMNS
import BoardDimensions.ROWS
import kotlinx.dom.removeClass
import org.w3c.dom.Element

class BoardDOM {
    val cells: Map<XY, Element> = createDOM()

    fun refresh(occupiedXYs: List<OccupiedXY>) {
        cells.values.forEach {
            it.className = "cell"
            it.textContent = ""
        }
        occupiedXYs.forEach {
            val cellElement = cells[it.xy]!!
            cellElement.textContent = it.letter
            cellElement.classList.add("cell-${it.shape.name.lowercase()}")
        }
    }

    fun markMatchedCells(matched: WordMatcher.Matched) {
        matched.rows.forEach { y ->
            ALL_Xs.forEach { x ->
                cells[XY(x, y)]!!.classList.add("cell-matched")
            }
        }
        matched.columns.forEach { x ->
            ALL_Ys.forEach { y ->
                cells[XY(x, y)]!!.classList.add("cell-matched")
            }
        }
    }

    fun clearMatchedCells() {
        cells.values.forEach { it.removeClass("cell-matched") }
    }

    companion object {
        private fun createDOM(): Map<XY, Element> {
            val map = mutableMapOf<XY, Element>()
            val boardEl = div(className = "board").appendTo(document.body!!)
            ALL_Ys.forEach { y ->
                val rowEl = div(className = "row").appendTo(boardEl)
                ALL_Xs.forEach { x ->
                    val cellEl = div(className = "cell").appendTo(rowEl)
                    map[XY(x, y)] = cellEl
                }
            }
            return map.toMap()
        }
    }
}