import kotlinx.browser.window
import kotlinx.coroutines.await
import kotlin.js.Promise
import kotlin.math.floor
import kotlin.math.roundToInt
import kotlin.random.Random
import kotlin.reflect.typeOf

class Words(private val words: List<String>) {

    fun matches(string: String): Boolean {
        return recursive(string, 0, words.size - 1)
    }

    private fun recursive(string: String, start: Int, end: Int): Boolean {
        if (start > end) return false
        val mid = floor((start + end).toFloat() / 2f).toInt()
        val midValue = words[mid]
        if (midValue == string) return true
        return if (midValue > string) recursive(string, start, mid - 1)
        else recursive(string, mid + 1, end)
    }
}

object WordsLoader {
    suspend fun fetch(language: Language): Words {
        return Promise.resolve(window.fetch("/words_${language.code}.txt"))
            .then { it.text() }
            .then { parse(it) }
            .await()
    }

    private fun parse(txt: String): Words {
        return Words(txt.lines())
    }
}
