import GameState.TETROMINO_SPAWNED
import kotlinx.browser.window
import kotlinx.coroutines.*

class GameManager(letters: Letters, val wordMatcher: WordMatcher) {
    private val boardDOM = BoardDOM()
    private val board = Board(letters)
    private var disabledInput = false
    private var job: Job? = null

    fun run() {
        window.onkeydown = {
            handleInput(it.keyCode)
        }
        startJob()
    }

    private fun restartJob() {
        stopJob()
        startJob()
    }

    private fun startJob() {
        job = GlobalScope.launch {
            while (true) {
                handleNewGameState(board.tick())
                delay(INTERVAL)
            }
        }
    }

    private fun stopJob() {
        job?.cancel()
    }

    private fun handleInput(keyCode: Int) {
        if (disabledInput) return
        val input = when (keyCode) {
            KeyCodes.A.code, KeyCodes.LEFT.code -> Input.MOVE_LEFT
            KeyCodes.D.code, KeyCodes.RIGHT.code -> Input.MOVE_RIGHT
            KeyCodes.S.code, KeyCodes.DOWN.code -> Input.MOVE_DOWN
            KeyCodes.W.code, KeyCodes.UP.code -> Input.DROP
            KeyCodes.E.code, KeyCodes.SPACE.code -> Input.ROTATE
            KeyCodes.DELETE.code -> Input.RESET
            else -> null
        }
        if (input in listOf(Input.DROP, Input.RESET)) {
            restartJob()
        }
        if (input != null) {
            board.input(input)
            boardDOM.refresh(board.xys)
        }
    }

    private suspend fun handleNewGameState(state: GameState) {
        boardDOM.refresh(board.xys)
        when (state) {
            TETROMINO_SPAWNED -> checkForMatches()
            else -> {}
        }
    }

    private suspend fun checkForMatches() {
        val matched = wordMatcher.match(board.placedXYs)
        if (matched.isEmpty()) return

        disabledInput = true
        boardDOM.markMatchedCells(matched)
        delay(1000)
        matched.rows.sorted().forEach(board::destroyRow)
        matched.columns.sorted().forEach(board::destroyColumn)
        boardDOM.clearMatchedCells()
        boardDOM.refresh(board.xys)
        delay(500)
        disabledInput = false
        restartJob()
    }


    companion object {
        private const val INTERVAL = 1000L
    }

    private enum class KeyCodes(val code: Int) {
        DELETE(46),
        A(65),
        LEFT(37),
        D(68),
        RIGHT(39),
        S(83),
        DOWN(40),
        W(87),
        UP(38),
        SPACE(32),
        E(69)
    }
}