import Direction.*
import GameState.*

class Board(private val letters: Letters) {
    var placedXYs: List<OccupiedXY> = emptyList()
        private set
    var activeXYs: List<OccupiedXY> = emptyList()
        private set
    val xys: List<OccupiedXY> get() = placedXYs + activeXYs

    fun tick(): GameState {
        return if (activeXYs.isNotEmpty()) {
            if (moveActive(DOWN, placeOnFailure = true)) TETROMINO_MOVED
            else spawnRandomTetromino()
        } else {
            spawnRandomTetromino()
        }
    }

    fun input(input: Input) {
        when (input) {
            Input.MOVE_LEFT -> moveActive(LEFT)
            Input.MOVE_RIGHT -> moveActive(RIGHT)
            Input.MOVE_DOWN -> moveActive(DOWN)
            Input.ROTATE -> rotate()
            Input.DROP -> while (activeXYs.isNotEmpty()) if (!moveActive(DOWN, placeOnFailure = true)) break
            Input.RESET -> reset()
        }
    }

    fun destroyRow(y: Int) {
        placedXYs = placedXYs.filter { it.xy.y != y }
        moveDownAllAboveRow(y)
    }

    fun destroyColumn(x: Int) {
        placedXYs = placedXYs.filter { it.xy.x != x }
    }

    private fun reset() {
        placedXYs = emptyList()
        activeXYs = emptyList()
    }

    private fun spawnRandomTetromino(): GameState = spawnTetromino(Tetromino.random(letters))

    private fun spawnTetromino(tetromino: Tetromino): GameState {
        val targetXYs = tetromino.localXYs.map { it + SPAWN_XY }
        return if (targetXYs.none { it.xy in placedXYs.xys() }) {
            activeXYs = targetXYs
            TETROMINO_SPAWNED
        } else GAME_OVER
    }

    private fun moveActive(direction: Direction, placeOnFailure: Boolean = false): Boolean {
        val targetXYs = activeXYs.map { it.move(direction) }
        val hitBounds = targetXYs.any { it.valid() == null }
        val spaceOccupied = targetXYs.any { it.xy in placedXYs.xys() }
        return if (hitBounds || spaceOccupied) {
            if (placeOnFailure) {
                placedXYs += activeXYs
                activeXYs = emptyList()
            }
            false
        } else {
            activeXYs = targetXYs
            true
        }
    }

    private fun moveDownAllAboveRow(y: Int) {
        val xysToMove = placedXYs.filter { it.xy.y < y }
        val xysToStay = placedXYs - xysToMove.toSet()
        val movedXYs = xysToMove.map { it.move(DOWN).valid()!! }
        placedXYs = xysToStay + movedXYs
    }

    private fun rotate(): Boolean {
        val targetXYs = activeXYs.rotate()
        val hitBounds = targetXYs.any { it.valid() == null }
        val spaceOccupied = targetXYs.any { it.xy in placedXYs.xys() }
        return if (hitBounds || spaceOccupied) {
            false
        } else {
            activeXYs = targetXYs
            true
        }
    }

    private fun List<OccupiedXY>.xys() = map { it.xy }

    private fun List<OccupiedXY>.rotate(): List<OccupiedXY> {
        if (isEmpty()) return emptyList()
        val pivot = first { it.isPivot }
        val movedToOrigin = map { it.copy(xy = it.xy - pivot.xy) }
        val rotated = movedToOrigin.map { it.copy(xy = XY(x = -it.xy.y, y = it.xy.x)) }
        return rotated.map { it.copy(xy = it.xy + pivot.xy) }
    }

    companion object {
        private val SPAWN_XY = XY(4, 0)
    }
}