import BoardDimensions.COLUMNS
import BoardDimensions.ROWS

data class XY(val x: Int, val y: Int) {
    fun valid(): XY? = if (x in 0..<COLUMNS && y in 0..<ROWS) this else null
    operator fun plus(other: XY) = XY(x + other.x, y + other.y)
    operator fun minus(other: XY) = XY(x - other.x, y - other.y)
    fun move(direction: Direction, distance: Int = 1) = when (direction) {
        Direction.LEFT -> XY(x - 1, y)
        Direction.RIGHT -> XY(x + 1, y)
        Direction.DOWN -> XY(x, y + 1)
    }
}